import React, {useState} from "react";
import { data } from "../utils/data";

export const NetworkTab = () => {
    const [isOpen, setIsOpen] = useState(false)
    return (
        <div className="">

            <div className="relative flex flex-col">
                <button onClick={()=>setIsOpen(!isOpen)} className="w-full p-4 flex items-center bg-blue-400 justify-between font-bold tracking-wider border-4 border-transparent actie:border-white">Dropdown</button>
            </div>
            <ul className="absolute top-20 flex flex-col items-start rounded-lg p-2 w-full">
                {
                    isOpen && (
                        data.map((item,i)=>{
                            return(
                                <li key={i}> <img src={item.icon} alt="icon" height="50" width="50"/> {item.name} </li>
                            )
                        })
                    )
                }
                
                </ul> 


        </div>
    )
}



                       